import '../assets/stylesheets/application.scss';
import './components/toggler';
import logo from '../assets/images/logo.svg';

const inputElement = document.querySelector('input[type="file"]');

function handleUpload() {
  const fileList = this.files;
  if (fileList.length > 0) {
    document.querySelectorAll('.wrapper h1, .wrapper h2, .file-upload').forEach(heading => heading.remove());

    document.querySelector('.wrapper').insertAdjacentHTML('afterbegin', `
      <h2>Analyzing video...</h2>
      ${logo}
    `);
  }
}

inputElement.addEventListener('change', handleUpload, false);
